import returns from '../../common/returns.vue'
export default {
	components: {
		returns
	},
	data() {
		return {
			title: '订单详情',
			data: '',
			data_list: '',
			pay_list: '',
			show: false,
			payment_id: '',
			payment_name: '',
			goods:'',
			order:'',
			new_aftersale_data:''
		}
	},
	methods: {
		jump(url) {
			uni.navigateTo({
				url: url
			})
		},
		cancel_return(id){//取消退款申请
			let that = this
			uni.showModal({
			    title: '提示',
			    content: '是否取消退款/退货申请？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'get',that.APIconfig.api_root.subuser.threeuser.g_AftersaleCancel,{
							id:id,
							user_id:that.$store.state.user.id,	
						},function(self,res){
							uni.showToast({
								icon:'none',
								title:res.msg
							})
			            	if(res.code == 0){
								setTimeout(function(){
									self.service.returns()
								},1500)
							}
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
		},
		
	},
	onLoad(options) {
		this.service.entire(this, 'get', this.APIconfig.api_root.subuser.threeuser.g_Aftersale, {
			oid: options.id,
			user_id:this.$store.state.user.id,
		}, function(self, res) {
			self.data = res.data.order.address
			self.goods = res.data.goods[0]
			self.order = res.data.order.order
			self.new_aftersale_data = res.data.new_aftersale_data
		})
	}
}
